.search {
  position: relative;
  display: flex;
  width: 400px;
  @media (max-width: 768px) {
    width: 100%;
    margin-bottom: 20px;
  }
}

.input {
  @media (max-width: 768px) {
    border-radius: 0px;
  }
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 12px 20px;
  padding-left: 42px;
  border-radius: 10px;
  font-size: 16px;
}

.icon {
  width: 24px;
  height: 24px;
  opacity: 0.3;
  position: absolute;
  left: 10px;
  top: 10px;
}

.clearIcon {
  width: 18px;
  height: 18px;
  opacity: 0.3;
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }
}
